  position: absolute;
  right: -70px;
  top: calc(100% + 22px);
  z-index: 99;
  width: 320px;
  background-color: #fff;
  padding: 22px;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
  border-radius: 0 0 4px 4px;
  border: 1px solid #3D565F;
}

.dropdown-cart.dropdown-open {
  display: block;
  visibility: visible;
}

.dropdown-account, .dropdown-cart {
  right: 0;
  display: none;
  visibility: hidden;
  position: absolute;
  top: calc(100% + 30px);
  border-radius: 8px;
  z-index: 99;
  width: 220px;
  background-color: var(--bs-neutral-0);
  padding: 22px;
