.mobile-account {
  margin: 15px 0;
  padding: 30px 0 20px 0;
  border-top: thin solid #E0E6F6;
}
.mobile-account h6 {
  font-weight: 600;
  font-size: 18px;
}
.mobile-account ul li {
  padding: 13px 0;
}
.mobile-account ul li a {
  font-size: 15px;
  line-height: 1;
  text-transform: capitalize;
  font-weight: 500;
  position: relative;
  display: inline-block;
  color: #253D4E;
  transition-duration: 0.2s;
}
.mobile-account ul li:hover a {
  padding-left: 3px;
  transition-duration: 0.2s;
  color: #000000;
}

.nav li {
  list-style: none;
}

#calendar-events {
  background-color: var(--bs-background-card);
}
#calendar-events .datepicker-inline {
  width: 100%;
}
#calendar-events .datepicker-inline .table-condensed {
  width: 100%;
}
#calendar-events .datepicker-inline .table-condensed thead tr .prev, #calendar-events .datepicker-inline .table-condensed thead tr .next, #calendar-events .datepicker-inline .table-condensed thead tr .datepicker-switch {
  display: none;
}
#calendar-events .datepicker-inline .table-condensed thead tr th {
  color: var(--bs-neutral-500);
  font-size: 14px;
  line-height: 22px;
  color: var(--bs-neutral-400);
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
}
#calendar-events .datepicker-inline .table-condensed thead tr th:hover {
  color: var(--bs-neutral-500);
}
#calendar-events .datepicker-inline .table-condensed tbody tr td {
  vertical-align: middle;
  font-size: 18px;
  line-height: 28px;
  height: 40px;
  font-weight: 500;
  color: var(--bs-neutral-1000);
  text-align: center;
}
#calendar-events .datepicker-inline .table-condensed tbody tr td.day.active {
  background-color: var(--bs-success);
  border-color: var(--bs-success);
  background-image: none;
  color: var(--bs-neutral-1000);
}
#calendar-events .datepicker-inline .table-condensed tbody tr td:hover {
  color: var(--bs-neutral-500);
}

.sidebar-border-bg {
  border: 1px solid #E0E6F7;
  padding: 30px 40px 260px 40px;
  border-radius: 4px;
  margin-bottom: 40px;
}
.sidebar-border-bg .text-grey {
  color: #B4C0E0;
  font-size: 24px;
  line-height: 37px;
  letter-spacing: 2px;
  text-transform: uppercase;
  display: block;
}
.sidebar-border-bg .text-hiring {
  color: #66789C;
  font-size: 36px;
  font-weight: 800;
  line-height: 57px;
  letter-spacing: 2px;
  text-transform: uppercase;
  display: inline-block;
  margin-top: -15px;
}

.nav {
  display: inline-block !important;
}

.btn-expanded {
  background: #ffffff url(../imgs/template/icons/burger.svg) no-repeat center;
  height: 42px;
  width: 42px;
  position: relative;
  padding: 0px;
}

.btn-collapsed {
  background: #ffffff url(../imgs/template/icons/burger.svg) no-repeat center;
}

.footer {
  position: absolute;
  bottom: 0px;
  left: 0px;
  z-index: 1234;
  right: 0px;
  background: #fff;
  padding: 30px 30px 0px 30px;
  border-top: 1px solid var(--bs-border-color);
}
.footer .box-footer {
  padding: 0px 0px 0px 0px;
}
.footer .menu-footer {
  display: inline-block;
}
.footer .menu-footer li {
  list-style: none;
  display: inline-block;
  padding-left: 22px;
}
.footer .menu-footer li a {
  color: #4F5E64;
  font-size: 14px;
  text-decoration: none;
  transition-duration: 0.2s;
}
.footer .menu-footer li a:hover {
  color: #000000;
  transition-duration: 0.2s;
  padding-left: 3px;
}

#scrollUp {
  width: 48px;
  height: 48px;
  color: #ffffff;
  right: 30px;
  bottom: 30px;
  border-radius: 30px;
  text-align: center;
  overflow: hidden;
  z-index: 999 !important;
  border: 0;
  background-color: #fff;
  transition-duration: 0.2s;
  background-color: #f09814;
  background-image: url(../imgs/template/icons/go-top.svg);
  background-repeat: no-repeat;
  background-position: center;
}
#scrollUp i {
  display: none;
  line-height: 42px !important;
  font-size: 20px;
  color: #fff;
}
#scrollUp:hover {
  transform: translateY(-3px);
  transition-duration: 0.2s;
}

